.background {
  height: 100%;
  width: 100%;
  float: left;
  top: 0%;
  left: 0%;
  right: 0%;
  bottom: 0%;
  padding: none;
  position: fixed;
  object-fit: cover;
  z-index: -1;
}
@keyframes fadein {
  form {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadein {
  animation: fadein 500ms ease-in-out;
}
